import { motion } from "framer-motion";
import OC_background from "./OC_Backdrop";
import WakeupTable from "./WakeupTable";
import SleepTable from "./SleepTable";

const dropIn = {
    hidden: {
        y: "-100vh",
        opacity : "0",
    },
    visible: {
        y: "0",
        transition: {
            duration: 0.1,
            type: "spring",
            damping: 25,
            stiffness: 200,
        },
        opacity : "1",
    },
    exit: {
        y: "100vh",
        opacity : "0"
    },

};

